/*--------------------首頁開始------------------*/
.section {
    padding: 72px 0 64px 0;
    @include Pad{
        padding: 48px 0 40px 0;
    }
}
.main-list {
    &-btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: $white !important;
        border-radius: 16px;
        border: 1px solid $white;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 100%;
        min-height: 250px;
        color: $black;
        padding: 32px 24px;
        @include PC{
            min-height: 200px;
        }
        @include PC-sm {
            flex-direction: row;
            min-height: auto;
            padding: 16px 32px;
        }
        @include Pad {
            font-size: $h6-font-size;
        }
        @include mobile-lg{
            padding: 16px;
        }
        &:hover {
            box-shadow: none;
        }
        img {
            max-width: 45%;
            margin-bottom: 16px;
            @include PC-sm {
                width: 100px;
                margin-bottom: 0;
                margin-right: 24px;
            }
            @include Pad {
                width: 80px;
                margin-right: 16px;
            }
        }
    }
    &-title {
        font-size: $h5-font-size;
        font-weight: $font-weight-semibold;
        @include PC-sm {
            font-size: $h5-font-size;
            margin-bottom: 4px;
        }
        @include Pad {
            font-size: $h6-font-size;
        }
    }
    &-con{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        @include PC-sm{
            align-items: flex-start;
            justify-content: flex-start;
        }
        p{
            @include Pad{
                font-size: $h8-font-size;
            }
        }
    }
}
/*--------------------圖表開始------------------*/
.chart{
    width: 100%;
    height: 340px;
    margin-top: -33px;
    overflow: hidden;
    @include PC-md{
        margin-top: -50px;
    }
    @include PC-sm{
        margin-top: 0;
    }
    @include mobile-lg{
        height: 400px;
    }
    @include mobile{
        height: 480px;
    }
    &-header{
        height: 54px;
        position: relative;
        z-index: 0;
        display: flex;
        align-items: center;
    }
    &-title{
        font-size: $h5-font-size;
        margin-bottom: 0;
    }
}
.btn-list {
    &-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 16px;
        border: 1px solid $white !important;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 150px;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include PC-sm {
            height: 100px !important;
        }
        @include Pad {
            font-size: $h6-font-size;
        }
        &:hover {
            border: 1px solid $primary !important;
            box-shadow: none;
        }
        img {
            width: 56px;
            @include PC-sm {
                width: 52px;
            }
            @include Pad {
                width: 48px;
            }
        }
    }
    p {
        padding: 0 16px;
        text-align: center;
        @include PC-sm {
            width: 195px;
        }
        @include Pad {
            width: 165px;
            padding: 0 8px;
        }
    }
}
.theme-card {
    width: 90%;
    max-width: 1286px;
    margin: 0 auto;
    padding: 0 80px;
    @include PC-md {
        width: 100%;
        padding: 0 72px;
    }
    @include PC {
        padding: 0 68px;
    }
    @include PC-sm {
        padding: 0 calc(5% - 12px);
    }
    @include Pad {
        width: 100%;
        max-width: 95%;
        padding: 0 10px;
    }
    @include mobile-lg {
        padding: 0 2%;
    }
    @include mobile-md {
        max-width: 98%;
        padding: 0 12px;
    }
    .slick-slide:nth-child(3n) {
        .theme-card-item {
            margin-right: 3px;
            @include PC {
                margin-right: 24px;
            }
        }
    }
    .slick-slide:nth-child(2n) {
        .theme-card-item {
            @include PC {
                margin-right: 3px;
            }
        }
    }
    &-item {
        display: block;
        background: $white;
        border-radius: 16px;
        overflow: hidden;
        border: 3px solid $gray-300;
        position: relative;
        margin: 0 24px 12px 0;
        @include mobile-md {
            margin: 0 0 12px 0 !important;
        }
        p {
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 135px;
            font-size: $h5-font-size;
            font-weight: $font-weight-medium;
            color: $black;
            padding: 0 16px;
            @include Pad {
                height: 100px;
                font-size: $h6-font-size;
            }
            @include mobile-md {
                padding: 0 40px;
            }
        }
        span {
            display: block;
            background: $primary-light;
            color: $primary-xdark;
            text-align: center;
            font-size: $h7-font-size;
            font-weight: $font-weight-medium;
            padding: 8px 8px;
            @include Pad {
                font-size: $h8-font-size;
            }
        }
        &:hover {
            border: 3px solid $primary-light;
            .theme-card-tag {
                &::before {
                    width: 100%;
                }
            }
        }
    }
    &-tag {
        border-radius: 0 8px 8px 0;
        background: $primary;
        position: absolute;
        width: 50px;
        height: 35px;
        left: 0;
        top: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        overflow: hidden;
        @include PC-sm {
            width: 40px;
            height: 30px;
        }
        @include Pad {
            top: 35px;
        }
        &:before {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            width: 0;
            height: 100%;
            background: $tertiary;
            transition: all 0.2s ease;
        }
        img {
            width: 25px;
            position: relative;
            z-index: 10;
        }
    }
}
