/* ---------------------登入頁開始------------------------*/
.login {
    min-height: calc(100vh - 46px);
    display: flex;
    flex-direction: column;
    &-logo {
        position: absolute;
        top: 24px;
        right: 24px;
        display: flex;
        align-items: center;
        font-weight: $font-weight-medium;
        @include Pad{
            font-size: $h7-font-size;
            top: 16px;
            right: 16px;
        }
        img {
            width: 32px;
            margin-right: 8px;
            @include Pad{
                width: 24px;
            }
        }
    }
    &-content {
        display: flex;
        justify-content: flex-end;
        background: url("../images/login-bg.jpg") right;
        background-size: cover;
        @include PC {
            background: url("../images/login-bg.jpg") -250px bottom $secondary-light no-repeat;
            background-size: 140%;
        }
        @include PC-sm {
            background: url("../images/login-bg-mobile-top.svg") center 40px no-repeat, url("../images/login-bg-mobile-bottom.svg") -250px bottom no-repeat $secondary-light;
            background-size: 80%, 800px;
        }
        @include Pad {
            justify-content: center;
            background: url("../images/login-bg-mobile-top.svg") center 40px no-repeat, url("../images/login-bg-mobile-bottom.svg") center bottom no-repeat $secondary-light;
            background-size: 80%, 80%;
        }
    }
    &-form {
        width: 90%;
        max-width: 485px;
        margin-right: 11%;
        min-height: calc(100vh - 51px);
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        @include PC-lg {
            max-width: 450px;
            margin-right: 10%;
        }
        @include PC{
            max-width: 380px;
            margin-right: 5%;
        }
        @include PC-sm {
            max-width: 350px;
            min-height: calc(100vh - 120px);
        }
        @include Pad{
            padding-top: 80px;
            padding-bottom: 150px;
            margin: 0;
        }
        @include mobile-lg{
            min-height: calc(100vh - 109px);
        }
        input {
            padding: 16px 16px 16px 60px;
            border: 1px solid $primary !important;
            @include PC-lg {
                padding: 12px 16px 12px 60px;
            }
            &[data-type="account"] {
                background: url("../images/ic-account.svg") no-repeat 16px center $white;
            }
            &[data-type="password"] {
                background: url("../images/ic-password.svg") no-repeat 16px center $white;
            }
        }
        &-title {
            display: block;
            margin-bottom: 32px;
            font-weight: $font-weight-semibold;
            font-size: 3.3rem;
            @include PC-lg {
                letter-spacing: 2px;
                font-size: 3rem;
            }
            @include PC {
                font-size: 2.5rem;
            }
            @include PC-sm {
                font-size: $h2-font-size;
            }
            @include Pad {
                font-size: 1.8rem;
            }
            @include mobile-lg{
                margin-bottom: 12px;
            }
        }
        &-btn {
            font-size: $h5-font-size;
            padding: 12px 0;
            @include PC-lg {
                padding: 9px 0;
            }
            @include Pad {
                font-size: $h6-font-size;
            }
        }
        &-link {
            display: flex;
            align-items: center;
            font-weight: $font-weight-medium;
            @include Pad {
                font-size: $h7-font-size;
                img {
                    width: 18px;
                }
            }
        }
    }
    &-footer {
        background: $secondary;
        color: $gray-800;
        padding: 12px 0;
        display: flex;
        justify-content: center;
        font-size: $h7-font-size;
        position: relative;
        border-top: 4px solid $white;
        @include PC-sm {
            font-size: $h7-font-size;
            flex-direction: column;
            padding: 12px 5%;
        }
        @include mobile-lg{
            font-size: $h8-font-size;
        }
        &-link {
            display: flex;
            align-items: center;
            color: $gray-800;
            &:hover {
                color: $primary;
            }
            img {
                width: 20px;
            }
        }
    }
}
