@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');

/* --------------基本設定開始--------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $gray-900;
	/*避免在觸控時文字被選取*/
	-webkit-user-select:none;
	-webkit-touch-callout:none;
	-moz-user-select: none;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus,button:focus{
	box-shadow: none !important;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
}
label{
	font-weight: $font-weight-medium;
	
}
.hr{
	background: $gray-300;
	position: relative;
	display: block;
	width: 100%;
	height: 2px;
	opacity: 1;
	&:before ,&:after{
		content: " ";
		display: block;
		width: 12px;
		height: 12px;
		background: $gray-300;
		border-radius: 50%;
		position: absolute;
		top: -5px;
	}
	&:before {
		left: 0;
	}
	&::after{
		right: 0;
	}
}
.col-form-label{
@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
input,textarea{
	border: 1px solid $primary-xdark !important;
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
select{
	border: 1px solid $primary-xdark !important;
	background: url("../images/ic-select-down.svg") no-repeat right center $white !important;
	padding-right: 24px !important;
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
.select2-dropdown{
	border: none;
	box-shadow:  0 5px 5px $gray-600;
}
.select2-selection__arrow{
	background: url("../images/ic-select-down.svg") no-repeat center $white;
	height: calc(100% - 2px) !important;
	b{
		display: none;
	}
}

.select2-results__message{
	font-size: $h7-font-size;
	color: $primary;
	padding-left: 12px;
}
input::placeholder,textarea::placeholder {
	color: $gray-500!important;
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
.btn{
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: $font-weight-medium;
	&:active{
		border: none;
	}
}
.form-check-input{
	border: 1px solid $primary-xdark !important;
}
.form-check-input:checked[type="checkbox"]{
	background-color: $primary;
	border: 1px solid $primary !important;
}
input[readonly]{
	border: 1px solid $primary-light !important;
	&:focus{
		box-shadow: none;
	}
}
input[type="file"]{
    text-align: right;
	appearance: none;
    &::-webkit-file-upload-button {
		content: "點擊選擇檔案";
        float: right;
        position: relative;
        top: -1px;
        right: -10px;
        @include text-indent;
        background: url("../images/ic-cloud-arrow-up-fill.svg") no-repeat left center $white !important;
        padding-left: 16px;
        border: none;
        width: 26px;
    }
}
.upload-custom-btn{
	justify-content: flex-start;
        background: url("../images/ic-cloud-arrow-up-fill.svg") no-repeat right $white !important;
        padding-left: 16px;
        border: 1px solid $primary-xdark !important;
        width: 26px;
		color: $gray-500 !important;
		font-weight: $font-weight-normal;
}
.form-check-input[type="radio"]{
	flex-shrink: 0;
}
.form-check-input[type="radio"]:checked {
    background: url("../images/ic-circle.svg") no-repeat center !important;
	background-size: 11px !important;
	@include Pad{
		background: url("../images/ic-circle.svg") no-repeat center !important;
		background-size: 8px !important;
	}
	&~.form-check-label{
		pointer-events: none;
	}
}
.form-check,select~label{
	@include mobile-lg{
		font-size: $h7-font-size !important;
	}
}
.fa-arrow-up{
	background: url("../images/ic-up-arrow.svg") no-repeat center !important;
	background-size: 20px!important;
	&:after{
		display: none;
	}
}
.fa-arrow-down{
	background: url("../images/ic-up-arrow.svg") no-repeat center !important;
	transform: rotate(180deg);
	background-size: 20px!important;
	&:after{
		display: none;
	}
}
@each $key, $value in $theme-colors {
    .form-check-#{$key} {
		.form-check-input{
			border: 1px solid $gray-600 !important;
		}
        .form-check-input:checked[type="checkbox"]{
	background-color: $value;
	border: 1px solid $value !important;
}
    }
}