/*-----------------無障礙快速鍵樣式---------------------*/
.key {
    color: $gray-300;
    font-size: $h8-font-size;
}
/*-----------------輪播樣式(圓形點點在圖外)---------------------*/
.carousel-indicators {
    bottom: -45px;
    [data-bs-target] {
        width: 12px;
        height: 12px;
        margin-right: 8px;
        margin-left: 8px;
        border-radius: 50%;
        background: $primary;
        border: 2px solid $white;
    }
    .active {
        background: $primary-xdark;
    }
}
/*-----------------麵包屑樣式---------------------*/
.breadcrumb {
    &-link {
        color: $gray-600;
        &:hover {
            color: $primary;
        }
    }
    &-item {
        font-weight: $font-weight-medium;
        font-size: $h7-font-size;
        @include Pad {
            font-size: $h8-font-size;
        }
        &.active {
            font-weight: $font-weight-semibold;
        }
    }
}
.breadcrumb-item + .breadcrumb-item::before {
    content: "";
    display: inline-block;
    width: 8px;
    height: 100%;
    background: url("../images/ic-route.svg") no-repeat center;
    margin-right: 8px;
}
/*-----------------標題樣式---------------------*/
.sub-title {
    font-size: $h5-font-size;
    border-bottom: 1px solid $primary;
    font-weight: $font-weight-bold;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    @include Pad {
        font-size: $h6-font-size;
        line-height: 1.5;
    }
    img {
        width: 32px;
        margin-right: 8px;
    }
}
/*-----------------連結樣式---------------------*/
.link-img {
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}
.link-list {
    display: flex;
    @include mobile-lg {
        flex-direction: column;
    }
    &-item {
        border-right: 1px solid $gray-400;
        @include mobile-lg {
            border-right: none;
            border-bottom: 1px solid $gray-400;
        }
        &:last-child {
            border-right: 0;
            @include mobile-lg {
                border-bottom: none;
            }
        }
    }
    &-link {
        display: block;
        font-weight: $font-weight-medium;
        padding: 0 16px;
        @include Pad {
            font-size: $h7-font-size;
        }
        @include mobile-lg {
            padding: 4px 0;
        }
    }
}
/*-----------------頁籤樣式---------------------*/
.nav-tabs {
    padding: 10px 0 0 10px;
    margin-left: -10px;
    margin-bottom: -1px;
    @include mobile-lg {
        margin-left: 0;
    }
    .nav-item {
        margin-bottom: 0;
    }
    .nav-link {
        background: $gray-200;
        color: $gray-700;
        border-radius: 8px 8px 0 0;
        padding: 6px 32px !important;
        border: none;
        font-weight: $font-weight-medium;
        display: flex;
        align-items: center;
        margin-right: 40px;
        position: relative;
        font-size: 1.25rem;
        flex-shrink: 0;
        @include PC-lg{
            margin-right: 32px;
        }
        @include PC {
            padding: 6px 16px !important;
            font-size: $h6-font-size;
        }
        @include Pad{
            margin-right: 12px;
        }
        @include mobile-md{
            padding: 8px 12px !important;
            margin-right: 4px;
            font-size: $h7-font-size;
        }
        &:last-child{
            margin-right: 0;
        }
        &:after {
            content: "";
            display: block;
            position: absolute;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 43px 0 0 25px;
            border-color: transparent transparent transparent $gray-200;
            right: -22px;
            bottom: -1px;
            @include PC{
                border-width: 36px 0 0 20px;
                right: -19px;
            }
            @include Pad{
                display: none;
            }
        }
        &.active {
            background: $white;
            color: $primary-xdark;
            border: 1px solid $gray-300;
            border-bottom: 2px solid $white;
            box-shadow: -1px 0 3px $gray-400;
            display: flex;
            align-items: center;
            font-weight: $font-weight-bold;
            img {
                filter: invert(39%) sepia(15%) saturate(1021%) hue-rotate(343deg) brightness(91%) contrast(88%);
            }
            &::before{
                content: "";
                display: block;
                position: absolute;
                width: 6px;
                height: 43px;
                box-shadow: 2px 3px 3px $gray-400;
                transform: rotate(-29deg);
                right: -8px;
                bottom: 0;
                z-index: 1;
                @include PC{
                    height: 38px;
                    right: -7px;
                }
                @include Pad{
                    display: none;
                }
            }
            &:after{
                border-color: transparent transparent transparent $white;
                z-index: 2;
            }
        }
        img {
            width: 22px;
            margin-right: 8px;
            @include Pad{
                display: none;
            }
        }
    }
}

/*-----------------按鈕樣式---------------------*/
.radio-list {
    display: flex;
    flex-wrap: wrap;
    &-item {
        margin-right: 12px;
        margin-bottom: 12px;
        &:last-child {
            margin-right: 0;
        }
    }
    &-btn {
        border: 1px solid $primary;
        box-shadow: 0 2px 2px $gray-500;
        @include Pad {
            font-size: $h7-font-size;
        }
        &:hover {
            border: 1px solid $primary;
            box-shadow: none;
        }
        &.active {
            color: $secondary;
            background: $secondary;
            box-shadow: none;
            border: none;
        }
    }
}
.shadow-link {
    &:hover {
        box-shadow: none !important;
    }
}
.opacity-link {
    &:hover {
        opacity: 0.8;
    }
}
/*-----------------步驟樣式---------------------*/
.step {
    max-width: 880px;
    margin: 0 auto;
    display: flex;
    background: url("../images/dots.svg") repeat-x center 40px;
    position: relative;
    @include Pad {
        background: url("../images/dots.svg") repeat-x center 30px;
        background-size: 200px;
    }
    @include mobile-lg {
        width: 425px;
    }
    &:before,
    &:after {
        content: "";
        display: block;
        position: absolute;
        width: 100px;
        height: 50px;
        background: $tertiary-light;
        top: 30px;
        @include Pad {
            width: 50px;
        }
    }
    &:before {
        left: 0;
    }
    &:after {
        right: 0;
    }
    &-item {
        display: flex;
        align-items: center;
        flex-direction: column;
        position: relative;
        z-index: 10;
        &-img {
            width: 80px;
            height: 80px;
            background: $white;
            border-radius: 50%;
            border: 3px solid $base;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            margin-bottom: 4px;
            @include Pad {
                width: 60px;
                height: 60px;
                border-width: 2px;
            }
            @include mobile-lg {
                width: 50px;
                height: 50px;
            }
            img {
                width: 40px;
                @include Pad {
                    width: 30px;
                }
                @include mobile-lg {
                    width: 20px;
                }
            }
            span {
                background: $base;
                color: $white;
                width: 25px;
                height: 25px;
                display: flex;
                justify-content: center;
                padding-top: 1px;
                border-radius: 50%;
                position: absolute;
                right: -10px;
                top: 0;
                line-height: 1;
                border: 2px solid $white;
                @include mobile-lg {
                    width: 20px;
                    height: 20px;
                    font-size: $h8-font-size;
                }
            }
        }
        &-txt {
            color: $base;
            font-size: $h6-font-size;
            font-weight: $font-weight-bold;
            text-align: center;
            @include Pad {
                font-size: $h7-font-size;
            }
        }
        &[data-status="now"] {
            .step-item-img {
                border: 7px solid $secondary;
                @include Pad {
                    border-width: 4px;
                }
                span {
                    background: $secondary;
                    color: $white;
                    top: -7px;
                }
                img {
                    filter: invert(52%) sepia(57%) saturate(456%) hue-rotate(85deg) brightness(101%) contrast(90%);
                }
            }
            .step-item-txt {
                color: $gray-700;
            }
        }
        &[data-status="done"] {
            .step-item-img {
                background: $secondary;
                border: none;
                span {
                    background: $secondary-light;
                    color: $white;
                    top: 0;
                }
                img {
                    filter: invert(52%) sepia(57%) saturate(456%) hue-rotate(85deg) brightness(500%) contrast(90%);
                }
            }
            .step-item-txt {
                color: $gray-700;
            }
        }
    }
}
/*-----------------文字樣式---------------------*/
.text-basic {
    font-size: $h7-font-size;
    font-weight: $font-weight-medium;
    @include Pad {
        font-size: $h8-font-size;
    }
}
/*-----------------列表樣式---------------------*/
.text-list {
    display: flex;
    @include PC-sm {
        flex-direction: column;
    }
    &-item {
        display: flex;
        font-size: $h7-font-size;
        font-weight: $font-weight-medium;
        position: relative;
        color: $gray-800;
        img {
            opacity: 0.8;
        }
    }
}
.notice-list {
    @include PC-sm {
        flex-direction: column;
    }
    &-item {
        font-size: $h7-font-size;
        color: $tertiary;
        font-weight: $font-weight-medium;
        margin-left: 22px;
        position: relative;
        @include Pad {
            font-size: $h8-font-size;
        }
        &:before {
            content: "※";
            display: block;
            color: $tertiary;
            position: absolute;
            left: -22px;
        }
    }
}
.number-list {
    /*文字數字列點*/
    padding-left: 24px;
    font-size: $h7-font-size;
    @include Pad {
        font-size: $h8-font-size;
    }
    &-item {
        list-style-type: decimal;
        margin-bottom: 4px;
    }
}
.dot-list {
    &-item {
        display: flex;
        align-items: start;
        font-size: $h7-font-size;
        margin-bottom: 4px;
        &::before {
            content: "";
            display: inline-block;
            width: 7px;
            height: 7px;
            background: $danger;
            margin-right: 12px;
            flex-shrink: 0;
            margin-top: 8px;
        }
    }
}
.tag-list {
    background: $white;
    &-item {
        border-bottom: 1px solid $primary-light;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex !important;
        align-items: start;
        color: $gray-800;
        padding: 16px 0;
        font-size: $h6-font-size;
        @include PC-sm {
            padding: 12px 0;
        }
        @include mobile-lg {
            font-size: $h7-font-size;
        }
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 17px;
            margin-right: 16px;
            margin-top: 2px;
            background: $tertiary;
            flex-shrink: 0;
            @include mobile-lg {
                margin-right: 12px;
            }
        }
        &:hover {
            text-decoration: underline !important;
        }
        p {
            @include over_text(2);
        }
    }
}
.icon-list {
    display: flex;
    @include Pad {
        flex-direction: column;
    }
    &-item {
        display: flex;
        margin-right: 16px;
        @include Pad {
            margin-bottom: 8px;
        }
        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
        img {
            width: 20px;
            margin-right: 8px;
        }
    }
}
.text-word-list {
    &-item {
        display: flex;
        font-weight: $font-weight-medium;
        @include Pad {
            font-size: $h7-font-size;
        }
    }
    span {
        flex-shrink: 0;
    }
}
/*-----------------表格樣式---------------------*/
.table-title {
    font-size: $h3-font-size;
    font-weight: $font-weight-normal;
    text-align: center;
    @include Pad {
        font-size: $h5-font-size;
    }
}
.table-rwd {
    min-width: 100%;
    position: relative;
    @include PC {
        min-width: 90%;
        margin: 0 auto;
        border: none;
    }
    & > thead {
        @include PC {
            display: none;
        }
        & > tr > th {
            font-weight: $font-weight-normal;
            background: $quaternary;
            color: $gray-800;
            vertical-align: middle;
            text-align: center;
            border-bottom: 1px solid $quaternary;
        }
    }
    & > tbody {
        border: 1px solid $gray-400;
        @include PC{
            border: none;
        }
    }
    & > tbody > tr > th {
        display: table-cell;
        font-weight: normal !important;
        padding: 16px 8px;
        text-align: center;
        border-bottom: 1px solid $gray-400;
        @include PC {
            display: flex;
            background: $quaternary;
            color: $gray-800;
            padding: 8px 16px;
            font-size: $h7-font-size;
        }
    }
    & > tbody > tr{
        @include PC {
            display: block;
            margin-bottom: 24px;
        }
    }
    & > tbody > tr > td {
        display: table-cell;
        padding: 16px 8px;
        font-weight: $font-weight-normal;
        text-align: center;
        @include PC {
            display: flex;
            text-align: left !important;
            padding: 8px 16px !important;
            position: relative;
            text-align: left;
            font-size: $h7-font-size;
            border-bottom: 1px solid $gray-400;
            &[data-th=""] {
                &::before {
                    display: none;
                }
            }
        }
        &:first-child {
            text-align: center;
            font-weight: $font-weight-normal;
            padding: 8px 20px !important;
            pointer-events: none;
            @include PC {
                display: flex;
                justify-content: space-between;
                background: $quaternary !important;
                color: $gray-800;
                pointer-events: initial;
                &::after {
                    content: "-";
                    display: block;
                    margin-left: auto;
                    font-size: $h3-font-size;
                    line-height: 0.5;
                    margin-top: -7px;
                }
                &:before {
                    color: $white;
                    font-weight: $font-weight-normal;
                }
            }
        }
        &:before {
            display: none;
            @include PC {
                flex-shrink: 0;
                content: attr(data-th) "：";
                display: inline-block;
                color: $black;
                font-weight: $font-weight-medium;
                min-width: 75px;
                text-align: right;
                flex-shrink: 0;
            }
        }
        &[data-th="none"] {
            &:before {
                display: none;
            }
        }
    }
    a {
        color: $primary-xdark;
        &:hover {
            opacity: 0.8;
        }
    }
    &.table-striped{
        border: none;
        &>tbody>tr:nth-of-type(odd) > *{
            background: $white !important;
            --bs-table-bg-type:none;
            border-bottom: none;
        }
        &>tbody>tr:nth-of-type(even) > *{
            background: $quaternary-light !important;
            border-bottom: none;
        }
        &>tbody>tr:nth-of-type(odd)>th,&>tbody>tr:nth-of-type(even)>th{
            @include PC{
                background: $quaternary !important;
            }
        }
        &>tbody>tr>td{
            @include PC{
                border-bottom: 1px solid $quaternary !important;
            }
        }
    }
}
.table-text{
    @include PC{
        font-size: $h7-font-size;
    }
}
.table-link {
    display: flex;
    justify-content: center;
}
.table-toggle {
    cursor: pointer;
}

/*-----------------卷軸樣式---------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray-300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-500;
    }
}
/*-----------------表單樣式---------------------*/
.input-search {
    display: flex;
    align-items: center;
    label {
        font-size: $h5-font-size;
    }
    input {
        border-radius: 50px 0 0 50px !important;
        border: 1px solid $primary !important;
        border-right: 0 !important;
        padding-left: 20px;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:focus {
            box-shadow: none;
        }
        &::placeholder {
            color: $gray-600 !important;
            @include PC-sm {
                font-size: $h7-font-size;
            }
        }
    }
    input ~ button {
        border-radius: 0 50px 50px 0 !important;
        border: 1px solid $primary;
        border-left: 0;
        background: $white !important;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:hover {
            img {
                opacity: 0.8;
            }
        }
    }
}
/*-----------------視窗樣式---------------------*/
.modal {
    &-header {
        align-items: flex-start;
        border-bottom: none;
        position: relative;
        font-size: $h5-font-size;
        font-weight: $font-weight-semibold;
        padding-left: 24px;
        padding-right: 24px;
        &:after {
            content: "";
            display: block;
            position: absolute;
            left: 24px;
            bottom: 0;
            width: calc(100% - 48px);
            height: 1px;
            background: $primary-light;
        }
        .btn-close {
            background: url("../images/ic-close.svg") no-repeat center;
            background-size: 80%;
            opacity: 1;
            @include text-indent;
            &:hover {
                opacity: 0.8;
            }
        }
    }
    &-content {
        border-radius: 16px !important;
    }
    &-body {
        font-size: $h6-font-size;
        @include mobile-lg {
            font-size: $h7-font-size;
        }
    }
}

/*-----------------頁數樣式---------------------*/

.page {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mobile-lg {
        flex-direction: column;
        align-items: center;
    }
    &-list {
        display: flex;
        justify-content: flex-end;
        border-right: 1px solid $gray-300;
        padding-right: 16px;
        margin-right: 16px;
        @include mobile-lg {
            margin-bottom: 12px;
            border-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &-item {
        padding: 0 4px;
        display: flex;
        align-items: center;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .page-link {
                border: 1px solid $gray-200;
                background: $gray-100;
            }
        }
        .form-control {
            width: 40px;
            height: 28px;
            color: $primary;
            border: 1px solid $primary;
            border-radius: 5px;
            margin: 0 5px;
            padding: 0 8px;
            text-align: center;
        }
    }
    &-link {
        border-radius: 4px;
        background: $white;
        border: 1px solid $primary;
        color: $primary;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s ease;
        &:hover {
            background: $gray-100;
        }
        &.active {
            border: 2px solid $primary;
            font-weight: $font-weight-medium;
        }
        img {
            width: 25px;
            margin-right: 0;
        }
        &-function {
            border-radius: 4px;
        }
    }
    &-form {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        color: $gray-600;
        span {
            flex-shrink: 0;
        }
        select {
            width: 64px;
            height: 28px;
            flex-shrink: 0;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
/*-----------------容器樣式---------------------*/
.file-container {
    display: flex;
    align-items: stretch;
    border-radius: 8px;
    border: 1px solid $secondary;
    padding: 4px;
    background: $white;
    width: 100%;
    span {
        background: $secondary;
        border-radius: 5px;
        padding: 0 8px;
        line-height: 0;
        color: $white;
        margin-right: 12px;
        display: flex;
        align-items: center;
    }
    p {
        font-size: $h7-font-size;
    }
}
.over-text{
    @include over_text(1);
}
/*-----------------mobile樣式---------------------*/
.border-mobile-0 {
    @include mobile-lg {
        border: none !important;
    }
}
.PC-md-100 {
    @include PC-md {
        width: 100% !important;
    }
}
.PC-100 {
    @include PC {
        width: 100% !important;
    }
}
.PC-sm-100 {
    @include PC-sm {
        width: 100% !important;
    }
}
.Pad-100 {
    @include Pad {
        width: 100% !important;
    }
}

.mobile-100 {
    @include mobile-lg {
        width: 100% !important;
    }
}
