.main {
	margin-top: 140px;
	@include PC-lg {
		margin-top: 120px;
	}
	@include PC{
		margin-top: 100px;
	}
	@include mobile-lg {
		margin-top: 96px;
	}
}

/*--------------------主選單開始------------------*/
.navbar {
	justify-content: center;
	background-size: cover;
	width: 100%;
	box-shadow: 0 3px 5px $gray-500;
	@include mobile-lg{
		background-size: 1200px;
	}
	&-brand {
		display: flex;
		align-items: center;
		font-size: $h6-font-size;
		@include PC{
			font-size: $h7-font-size;
		}
		@include mobile-lg{
			font-size: $h8-font-size;
			padding-bottom: 0;
		}
		img{
			width: 24px;
			@include mobile-lg{
			width: 16px;
		}
		}
	}
	&-nav{
		height: 40px;
		flex-direction: row !important;
		@include mobile-lg{
			border: none;
			background: none;
			height: 36px;
		}
		.nav-item{
			display: flex;
			align-items: center;
			@include PC{
				font-size: $h7-font-size;
			}
			@include mobile-lg{
				font-size: $h8-font-size;
				img{
					width: 16px;
				}
				.btn{
					font-size: $h8-font-size;
				}
			}
		}
	}
	&-header{
		background: url("../images/header-bg.png") no-repeat right;
		background-size: contain;
		height: 100px;
		display: flex;
		align-items: center;
		justify-content: flex-start !important;
		@include PC-lg{
			height: 80px;
		}
		@include PC{
			height: 60px;
		}
		@include PC-sm{
			background: url("../images/header-bg.png") no-repeat 460px top;
			background-size: contain;
		}
		@include Pad{
			background: url("../images/header-bg.png") no-repeat 400px top;
			background-size: contain;
		}
		@include mobile-lg{
			background: url("../images/header-bg.png") no-repeat 200px top;
			background-size: cover;
			padding-bottom: 4px;
			flex-direction: column;
			align-items: start !important;
		}
	}
	&-title{
		font-size: 2.5rem;
		letter-spacing: 1px;
		font-weight: $font-weight-semibold;
		margin-bottom: 0;
		@include PC-lg{
			font-size: $h2-font-size;
		}
		@include PC{
			font-size: $h4-font-size;
		}
		@include mobile-lg{
			font-size: $h5-font-size;
		}
	}
}
/*--------------------側邊選單開始------------------*/
.side-menu {
	width: 340px;
	height: calc(100% - 140px);
	flex-shrink: 0;
	transition: all 0.2s ease;
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 150;
	background: $white;
	box-shadow: 0 3px 5px $gray-500;
	@include PC-lg{
		height: calc(100% - 120px);
	}
	@include PC {
		height: calc(100% - 100px);
		width: 280px;
	}
	@include PC-sm {
		position: fixed;
	}
	@include mobile-lg {
		height: calc(100% - 96px);
	}
	&-header {
		font-size: $h5-font-size;
		color: $white;
		text-align: center;
		font-weight: $font-weight-semibold;
		padding: 24px 0 16px 0;
		margin-bottom: 0;
		border-bottom: 2px solid $white;
		@include PC {
			font-size: $h6-font-size;
		}
	}
	&-item {
		background: none;
		border: none;
		border-radius: 0 !important;
	}
	&-accordion{
		li:first-child{
			.side-menu-link{
				padding-top: 16px;
			}
		}
		li:last-child {
			.side-menu-link{
				padding-bottom: 16px;
				&:after{
					display: none;
				}
			}
		}
	}
	&-link {
		background: none !important;
		display: flex;
		align-items: center;
		padding: 8px 16px 8px 55px;
		border-radius: 10px 0 0 10px;
		position: relative;
		font-weight: $font-weight-medium;
		color: $gray-900 !important;
		@include PC{
			padding-left: 26%;
		}
		&:after{
			content:"";
			position: absolute;
			display: block;
			width: calc(100% - 32px);
			left: 16px;
			bottom: 0;
			height: 1px;
			background: $primary;
		}
		&:hover {
			opacity: 0.8;
		}
		&.active {
			font-weight: $font-weight-semibold;
			color: $primary !important;
		}
	}
	&-btn{
		background: none !important;
		border: none !important;
		border-bottom: 2px solid $white;
		box-shadow: none !important;
		border-radius: 0 !important;
		position: relative;
		font-size: $h5-font-size;
		justify-content: flex-start;
		padding: 12px 12px 12px 32px !important;
		@include PC-sm{
			font-size: $h6-font-size;
		}
		&:before{
			content: " ";
			display: block;
			position: absolute;
			width: calc(100% - 32px);
			height: 1px;
			background: $gray-400;
			left: 16px;
			bottom: 0;
		}
		&:after{
			background: url("../images/ic-dropdown.svg") center !important;
			transform: rotate(180deg);
			position: absolute;
			right: 24px;
			top: 18px;
			@include PC{
				right: 16px;
			}
		}
		img{
			width: 26px;
		}
	}
	&-btn:not(.collapsed){
		background: $primary !important;
		color: $white;
		font-weight: $font-weight-semibold;
		&:after{
			background: url("../images/ic-dropdown-white.svg") center !important;
			transform: rotate(0deg);
		}
		img{
			filter: brightness(500);
		}
	}
	&-toggler {
		background: $primary;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: $white !important;
		width: 32px;
		height: 124px;
		border-radius: 0 8px 8px 0 !important;
		position: absolute;
		right: -32px;
		top: 110px;
		line-height: 1.2;
		z-index: 10;
		transition: all 0.2s ease;
		border: none;
		&:hover {
			background: $primary-dark !important;
		}
		&:active,
		&:focus {
			background: $primary !important;
			outline: none !important;
		}
		img {
			width: 18px;
			margin-right: 0;
			margin-bottom: 3px;
			transform: rotate(180deg);
		}
	}
	&.close {
		position: fixed;
		@include PC-sm {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(0deg);
			}
		}
	}
	&.fixed {
		position: fixed;
		top: 71px;
	}
}
.side-content {
	padding: 32px 3%;
	margin-left: 340px;
	width: calc(100% - 340px);
	@include PC {
		margin-left: 280px;
		width: calc(100% - 280px);
	}
	@include PC-sm {
		padding: 24px;
		margin-left: 0;
		width: 100%;
	}
	@include Pad{
		padding: 16px;
	}
	&-header {
		position: relative;
		@include PC-sm {
			padding-top: 32px;
		}
	}
	&-title {
		color: $gray-900;
		font-weight: $font-weight-semibold;
		font-size: 2.1rem;
		display: flex;
		align-items: center;
		@include PC{
			font-size: $h3-font-size;
		}
		@include Pad{
			font-size: $h4-font-size;
		}
		img{
			width: 45px;
			filter: brightness(-10%);
			@include PC{
				width: 40px;
			}
		}
	}
	&-breadcrumb{
		position: absolute;
		right: 0;
		top: 12px;
		@include PC-sm{
			top: 0;
		}
	}
}
.side-container {
	background: $white;
	border-radius: 10px;
	box-shadow: 0 3px 6px $gray-400;
}
.side-container-header {
	border-bottom: 2px solid $secondary;
	z-index: 1;
	ul {
		display: flex;
	}
	li {
		width: 100%;
		@include PC-lg {
			width: auto;
			flex-shrink: 0;
		}
		&:first-child {
			a {
				background: none;
			}
		}
	}
	a {
		background: url("../images/ic-gonext.svg") no-repeat left center;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px;
		background-size: 10px;
		width: 100%;
		font-weight: $font-weight-bold;
		color: $gray-800;
		position: relative;
		@include PC-lg {
			padding: 20px 24px;
		}
		img {
			margin-right: 8px;
			width: 20px;
		}
		&.active {
			color: $secondary;
			&::after {
				content: "";
				width: 100%;
				height: 5px;
				left: 0;
				bottom: 0;
				background: $secondary;
				position: absolute;
			}
			img {
				filter: invert(56%) sepia(29%) saturate(722%) hue-rotate(96deg) brightness(97%) contrast(92%);
			}
		}
	}
}
/*--------------------footer開始------------------*/
.footer {
	width: calc(100% - 340px);
	margin-left: 340px;
	padding: 16px 0;
	background: $gray-200;
	color: $gray-800;
	font-size: $h8-font-size;
	z-index: 100;
	position: relative;
	@include PC-lg{
		padding-left: 150px;
	}
	@include PC-md {
		padding-left: 16px;
		padding-top: 16px;
	}
	@include PC{
		width: calc(100% - 280px);
		margin-left: 280px;
	}
	@include PC-sm{
		width: 100%;
		margin-left: 0;
	}
	&-logo{
		width: 130px;
		@include PC-lg{
			position: absolute;
			left: 0;
			top: calc(50% - 19px);
		}
		@include PC-md{
			left: 24px;
			top: 12px;
		}
	}
	&-inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include PC-lg{
			justify-content: flex-start;
		}
		@include PC-md{
			flex-direction: column;
			align-items: flex-start;
		}
		p{
			@include PC-lg{
				display: flex;
				flex-direction: column;
				font-size: $h8-font-size;
			}
			@include PC-md{
				flex-direction: row;
				flex-wrap: wrap;
			}
		}
	}
	&-gotop{
		display: block;
		position: fixed;
		right: 16px;
		bottom: 48px;
		width: 56px;
		height: 56px;
		border-radius: 50%;
		background: $tertiary;
		color: $white !important;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border: 2px solid $white;
		font-size: $h7-font-size;
		font-weight: $font-weight-medium;
		img{
			width: 16px;
		}
	}
	a{
		color: $bright;
	}
}

